import React from 'react';
import Header from "./docs/Header"
import Body from "./docs/Body"
import Footer from "./docs/Footer"
import "./App.css"

function App() {
  React.useEffect(() => {

    const ScrollToTop = () => {
      window.scrollTo(0, 0);
      return null;
    };
    ScrollToTop();

  }, [])
  return (
    <div className="App-wrapper">
      <div className="App">
        <Header />
        <Body />
        <Footer />
      </div>
    </div>
  );
}

export default App;
